import React, { useRef } from "react";
import * as S from "./styles";
import Layout from "../../layout";
import Summary from "../../components/Summary/summary";
import emailjs from "emailjs-com";

function SaibaMais() {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_zsta39k",
        "template_2fvynjr",
        e.target,
        "user_SUt0coleMclOvZfAaj8rG"
      )
      .then(
        (result) => {},
        (error) => {}
      );
  };

  return (
    <Layout>
      <S.Container>
        <S.ContainerItem>
          <S.Span size="20" className="pt-5">
            CEO JORGE DURO
          </S.Span>

          <iframe
            style={{ width: "80vw", height: "40vh", marginTop: "1vh" }}
            src="https://www.youtube.com/embed/l0-0sZaho84"
            title="YouTube video player"
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>

          <S.Span className="m-5" size="30" cor="#969090">
            <a href="https://www.instagram.com/jorgeduro_/">
              <i className="bi bi-instagram m-2 btn btn-dark"></i>
            </a>
            <a href="https://www.linkedin.com/in/jorge-alberto-zietlow-duro-phd/">
              <i className="bi bi-linkedin btn btn-dark"></i>
            </a>
          </S.Span>
          <S.Span size="20" className="pt-5">
            Dúvidas frequentes
          </S.Span>
          <Summary
            title="O que é o Palco Web?"
            texto="O PalcoWeb é uma plataforma digital que tem como propósito oferecer serviço de streaming de qualidade para conteúdos culturais Made in Brasil para o Mundo e para Todos."
          ></Summary>
          <Summary
            title="Precisa pagar para se cadastrar no Palco Web?"
            texto="Não. Basta criar um cadastro para navegar na plataforma, temos produtos que são disponibilizados gratuitamente."
          ></Summary>
          <Summary
            title="Quais as vantagens do Palco web? #AGORAPODE"
            texto="Com o PalcoWeb você pode comer, beber, comentar idéias com amigos, tomar conta do seu pimpolho, e até atender chamadas de celular enquanto vc assiste uma peça ou um show no conforto do seu lar.
            Para as produtoras é possível fazer uma apresentação presencial numa cidade ou região e transmitir online para o resto do Brasil e do mundo."
          ></Summary>
          <Summary
            title="Para quem é o Palco Web?"
            texto="Para todos amantes de cultura que não dispensam o custo beneficio,e ainda assim agregar cultura em seu cotidiano."
          ></Summary>
          <Summary
            title="Tem apenas teatro no Palco Web?"
            texto="Nós do palco web temos o intuito de enriquecer nossa plataforma com diversas manifestações culturais audiovisuais, seja com teatro, shows ou ate mesmo palestras."
          ></Summary>
          <Summary
            title="O que é  TV Palco Web?"
            texto="Programa de entrevista e palestras com diretores, produtores e artistas da área de teatro e cultural."
          ></Summary>
          <Summary
            title="PalcoWeb em óculos VR?"
            texto="Em breve lançaremos peças em realidade virtual, implementando futuramente nossa interação com o METAVERSO cultural do PalcoWeb."
          ></Summary>

          <S.Span size="20" className="pt-5">
            Tive problemas
          </S.Span>
          <Summary
            title="Problemas com login"
            texto="Fale conosco no formulário abaixo."
          ></Summary>
          <Summary title="Problemas com pagamento">
            <br />
            •Verifique se o seu cartão é de uma das bandeiras <br />
            •Certifique-se que os dados do cartão estão preenchidos corretamente
            <br />
            •Proceda com o pagamento.
            <br />
            •Não deu certo? Entre em contato abaixo que iremos responder o mais
            breve possível.
          </Summary>
          <Summary
            title="Problemas na platafomra"
            texto="Fale conosco no formulário abaixo."
          ></Summary>
          <Summary
            title="Cancelamento"
            texto="Fale conosco no formulário abaixo."
          ></Summary>

          <S.Span size="20" className="pt-5">
            Palco Web na sua TV
          </S.Span>

          <iframe
            style={{ width: "80vw", height: "30vh", marginTop: "1vh" }}
            src="https://player.vimeo.com/video/677309811?h=e78daa89e1&amp;badge=0&amp;autopause=0&amp;player_id=0&amp;app_id=58479"
            title="espelhamento_celular_e_wifi_2 (Original)"
          ></iframe>

          <S.Form ref={form} onSubmit={(e) => sendEmail(e)}>
            <S.Span size="20" cor="#fdcc12" className="p-5">
              Fale Conosco
            </S.Span>
            <S.InputGroup>
              <S.Inpute
                right="10px"
                placeholder="Seu nome"
                name="name"
              ></S.Inpute>
              <S.Inpute placeholder="Celular"></S.Inpute>
            </S.InputGroup>
            <S.Inpute
              top="10px"
              type="email"
              placeholder="E-mail"
              name="email"
            ></S.Inpute>
            <S.Inpute
              top="10px"
              placeholder="Assunto do e-mail"
              name="subject"
            ></S.Inpute>
            <S.TexteArea
              top="10px"
              placeholder="Sua mensagem"
              name="message"
            ></S.TexteArea>
            <button type="submit" className="btn btn-warning p-2 m-5">
              Enviar
            </button>
          </S.Form>
        </S.ContainerItem>
      </S.Container>
    </Layout>
  );
}

export default SaibaMais;
