import React, { useRef, useState, useEffect } from "react";
import * as S from "./styles.js";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { useHistory } from "react-router-dom";

import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import { useShows } from "../../dados/context/ShowsContext";
import Auth from "../popups/Auth/index.js";

export default function ChannelsCarousel({ children, spectacles }) {
  const {
    getAllChannels,
    channelLike,
    isInFavoritesChannel,
    getAllChannelsFavoriteByUser,
  } = useShows();
  const { userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";
  const history = useHistory();

  useEffect(() => {
    getAllChannels();
  }, []);

  useEffect(() => {
    userId && getAllChannelsFavoriteByUser(userId, false);
  }, [userId]);

  const [scrollPerClick, setScrollPerClick] = useState(0);

  const slider = useRef(null);

  function handleNext() {
    if (
      scrollPerClick >
      slider.current.scrollWidth - slider.current.clientWidth
    )
      return;
    setScrollPerClick((prevState) => prevState + 200);
  }

  useEffect(() => {
    slider.current.scrollTo({
      top: 0,
      left: scrollPerClick,
      behavior: "smooth",
    });
  }, [scrollPerClick]);

  function handlePrevious() {
    if (scrollPerClick <= 0) return;
    setScrollPerClick((prevState) => prevState - 200);
  }

  useEffect(() => {
    slider.current.scrollTo({
      top: 0,
      left: scrollPerClick,
      behavior: "smooth",
    });
  }, [scrollPerClick]);

  return (
    <>
      <S.SectionContainer>
        <S.NavPrev onClick={() => handlePrevious()}>
          <NavigateBeforeIcon style={{ fontSize: 50 }}></NavigateBeforeIcon>
        </S.NavPrev>
        <S.NavNext onClick={() => handleNext()}>
          <NavigateNextIcon style={{ fontSize: 50 }}></NavigateNextIcon>
        </S.NavNext>
        <S.ListRow ref={slider}>
          {spectacles.length > 0 &&
            spectacles
              .map((item, key) => {
                return (
                  <S.ListItem src={item?.thumbnail} key={key}>
                    <S.InfoCard>
                      <S.Span size="14" style={{ marginBottom: "15px" }}>
                        {userIsAuthenticated() && (
                          <S.Coracao
                            channelIsInFavorites={isInFavoritesChannel(
                              item?.id
                            )}
                            onClick={() => {
                              channelLike(userId, item?.id);
                            }}
                          >
                            <i className="bi bi-suit-heart"></i>
                          </S.Coracao>
                        )}
                      </S.Span>
                      <S.Span size="14">{item?.fullName}</S.Span>

                      <S.Span size="10">
                        <i className="bi bi-circle-fill"></i>{" "}
                        <S.Span size="15">{item?.categoria}</S.Span>{" "}
                      </S.Span>
                      <S.Span></S.Span>
                      <S.verMais
                        size="14"
                        style={{ marginTop: "15px" }}
                        onClick={() => {
                          if (item.id === "rYChohhIQilAPofzyzNO") {
                            history.push(`/canalpw`);
                          } else {
                            history.push(`FichaCanal/${item?.id}`);
                          }
                        }}
                      >
                        Ver mais
                      </S.verMais>
                    </S.InfoCard>
                  </S.ListItem>
                );
              })
              .sort(function (a, b) {
                if (!window.location.href.includes("espetaculos")) {
                  if (
                    a?.fullName?.includes("palcoweb") ||
                    b?.fullName?.includes("palcoweb")
                  ) {
                    return 1;
                  }
                  if (
                    !a?.fullName?.includes("palcoweb") ||
                    !b?.fullName?.includes("palcoweb")
                  ) {
                    return -1;
                  }
                  return 0;
                }
              })}
        </S.ListRow>
      </S.SectionContainer>
    </>
  );
}
