import React from "react";
import { Route, Switch, HashRouter } from "react-router-dom";
import AdmLive from "../pages/AdmLive";
import Assinatura from "../pages/Assinatura/index";
import Authentication from "../pages/Authentication";
import Cadastro from "../pages/Cadastro/index";
import CadastroCart from "../pages/CadastroCart/index";
import Canais from "../pages/Canais";
import Colaboracao from "../pages/Colaboracao";
import Cardezao from "../pages/Cardezao";
import Cart from "../pages/Cart";
import Categories from "../pages/Categories";
import ComponenteVideo from "../pages/ComponenteVideo/index";
import ConfirmCompra from "../pages/ConfirmCompra/index";
import ContentMangement from "../pages/ContentManagement";
import Espetaculos from "../pages/Espetaculos/index";
import Espetaculoso from "../pages/Espetaculoso/index";
import EsqueciSenha from "../pages/EsqueciSenha/index";
import Favorites from "../pages/Favorites";
import FichaCanal from "../pages/FichaCanal";
import GiftShop from "../pages/GiftShop";
import GiftShopAdmin from "../pages/GiftShopAdmin";
import Home from "../pages/Home";
import Login from "../pages/Login/index";
import LoginCart from "../pages/LoginCart/index";
import Loja from "../pages/Loja";
import MeuPerfil from "../pages/MeuPerfil/index";
import Profile from "../pages/Profile";
import PurchaseDetails from "../pages/PurchaseDetails";
import SaibaMais from "../pages/SaibaMais";
import Signature from "../pages/Signature";
import Stats from "../pages/Stats";
import Watch from "../pages/Watch";
import PrivateRoutes from "./PrivateRoutes";
import AssinaturaConteudo from "../pages/AssinaturaConteudo";
import CanalPw from "../pages/CanalPw";
import StripeContainer from "../components/Stripe/StripeContainer";
import ScrollToTop from "../components/ScrollToTop";
import PlayaCanalPw from "../pages/PlayCanalPw";
import Pagamento from "../pages/Pagamento";

const Routes = () => {
  return (
    <HashRouter>
      <Switch>
        <Route path="/stats/:id">
          <ScrollToTop />
          <Stats />
        </Route>
        <Route path="/content/management/:id">
          <ScrollToTop />
          <ContentMangement />
        </Route>

        <PrivateRoutes
          path="/playespetaculo/:id/:id2/:id3"
          role="CLIENTE,MARKETING"
        >
          <ScrollToTop />
          <ScrollToTop />
          <ComponenteVideo />
        </PrivateRoutes>

        <Route path="/SaibaMais">
          <ScrollToTop />
          <SaibaMais />
        </Route>

        <Route path="/Canais">
          <ScrollToTop />
          <Canais />
        </Route>

        <Route path="/Assinatura">
          <ScrollToTop />
          <Assinatura />
        </Route>

        <Route path="/EsqueciSenha">
          <ScrollToTop />
          <EsqueciSenha />
        </Route>

        <Route path="/admin/live">
          <ScrollToTop />
          <AdmLive />
        </Route>

        <Route path="/watch/:id">
          <ScrollToTop />
          <Watch />
        </Route>
        <Route path="/profile">
          <ScrollToTop />
          <Profile />
        </Route>
        <Route path="/signature">
          <ScrollToTop />
          <Signature />
        </Route>
        <Route path="/favorites">
          <ScrollToTop />
          <Favorites />
        </Route>

        <Route path="/LoginCart/:id">
          <ScrollToTop />
          <LoginCart />
        </Route>

        <Route path="/login">
          <ScrollToTop />
          <Login />
        </Route>

        <Route path="/canalpw">
          <ScrollToTop />
          <CanalPw />
        </Route>

        {/* Logado e com assinatura ativa */}
        <PrivateRoutes
          exact
          path="/espetaculos/assinante"
          role="CLIENTE,MARKETING"
          // isSignature
        >
          <ScrollToTop />
          <AssinaturaConteudo />
        </PrivateRoutes>

        <PrivateRoutes exact path="/espetaculos" role="CLIENTE">
          <ScrollToTop />
          <Espetaculos />
        </PrivateRoutes>

        <PrivateRoutes exact path="/espetaculos/mkt" role="MARKETING">
          <ScrollToTop />
          <Espetaculoso />
        </PrivateRoutes>

        {/* Logado e com assinatura ativa */}
        <PrivateRoutes path="/perfil" role="CLIENTE,MARKETING">
          <ScrollToTop />
          <MeuPerfil />
        </PrivateRoutes>

        <Route path="/Cadastro/:id">
          <ScrollToTop />
          <CadastroCart />
        </Route>

        <Route path="/colaboracao">
          <ScrollToTop />
          <Colaboracao />
        </Route>

        <Route path="/Cadastro">
          <ScrollToTop />
          <Cadastro />
        </Route>

        <PrivateRoutes path="/ConfirmCompra/:id" role="CLIENTE">
          <ScrollToTop />
          <ConfirmCompra />
        </PrivateRoutes>

        <PrivateRoutes path="/pagamento" role="CLIENTE">
          <ScrollToTop />
          <Pagamento />
        </PrivateRoutes>

        {/* <Route path="/preview">
                  <ScrollToTop />
              <ContetPreview />
            </Route> */}
        <Route path="/auth">
          <ScrollToTop />
          <Authentication />
        </Route>
        <Route path="/giftshop">
          <ScrollToTop />
          <GiftShop />
        </Route>
        <Route path="/admin/giftshop">
          <ScrollToTop />
          <GiftShopAdmin />
        </Route>
        <PrivateRoutes path="/cart" role="CLIENTE,MARKETING">
          <ScrollToTop />
          <Cart />
        </PrivateRoutes>

        <Route path="/Loja">
          <ScrollToTop />
          <Loja />
        </Route>

        <Route path="/categorias">
          <ScrollToTop />
          <Categories />
        </Route>
        <Route path="/purchases/details">
          <ScrollToTop />
          <PurchaseDetails />
        </Route>
        <Route path="/FichaCanal/:id">
          <ScrollToTop />
          <FichaCanal />
        </Route>
        <Route path="/cardezao/:id">
          <ScrollToTop />
          <Cardezao />
        </Route>

        <Route path="/playcanalpw/:id1/:id2">
          <ScrollToTop />
          <PlayaCanalPw />
        </Route>

        <Route path="/">
          <ScrollToTop />
          <Home />
        </Route>
      </Switch>
    </HashRouter>
  );
};

export default Routes;
