import styled from "styled-components";
import InputMask from "react-input-mask";

export const Container = styled.div`
  width: 100vw;
  margin-top: 15vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  color: white;
  position: relative;
`;

export const CardContainer = styled.div`
  border: #fff 1px solid;
  border-radius: 5px;
  display: flex;
  width: 100%;
  padding: 0px 10px;
`;

export const CampForm = styled.form`
  display: flex;
  flex-direction: none;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 400px;
  margin: 20px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 5px 5px 5px 5px rgba(127, 255, 212, 0.1);
`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 1rem;
  font-size: ${(props) => props.size + "rem"};
  text-align: center;
  font-weight: ${(props) => props.bold};
`;

export const Span2 = styled.span`
  color: ${(props) => props.cor && !props.disabled?props.cor:props.disabled?'rgba(255,255,255,.2)': "white"};
  font-size: 1rem;
  font-size: ${(props) => props.size + "rem"};
  display: flex;
  gap: 2px;
  align-items: center;
  cursor: ${(props) =>!props.disabled?'pointer':'default'};
  transition: all 0.2s ease-in-out;

  &:hover {
    color: ${(props) =>!props.disabled && '#fdcc12'};
  }

  &:active {
    color: ${(props) =>!props.disabled && '#fdcc12'};

  }
  @media (max-width: 550px) {
    &:hover {
      color: ${(props) =>!props.disabled && '#fdcc12'};
    }
  }
`;

export const InputCardNumber = styled.input`
  width: 80vw;
  height: 5vh;
  margin-top: 0;
  padding-left: 30px;
  background-color: white;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.3);
  input-placeholder: black;
  font-weight: 500;
  font-size: 16px;
  border-radius: 4px;
  &::-webkit-input-placeholder {
    color: #aaa;
  }
`;
export const InputTitular = styled.input`
  width: 80%;
  height: 50px;
  background-color: white;
  color: black;
  border: 1px solid rgba(0, 0, 0, 0.3);
`;
export const CampoTexto = styled.div`
  width: 80%;
`;

export const CampoData = styled.div`
  width: 80%;
`;
export const Select = styled.select`
  width: 20%;
  height: 40px;
  margin: 5px;
  padding: 5px;
`;
export const Input = styled(InputMask)`
  width: ${(props) => (props.size ? props.size + "px" : "100%")};
  margin: 0 !important;
`;

export const Button = styled.button`
  background-color: #fdcc12;
  color: #1d1d1d;
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 !important;
  border: none;
  border-radius: 5px;
  width: 150px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3ms;
  :hover {
    background-color: #fdcc10;
    color: #000;
  }
  :disabled {
    background-color: gray;
    color: #fff;
    cursor: default;
  }
`;

export const ButtonVoltar = styled.button`
  text-transform: uppercase;
  font-size: 1rem;
  border: none;
  padding: 10px;
  color: #fff !important;
  transition: all 0.3s;
  background: transparent !important;
  &:hover {
    color: #d7ac02;
  }
`;

export const Form = styled.form`
  width: 100%;
  max-width: 768px;
  /* min-width: 500px; */
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
`;

export const Detalhes = styled.div`
  width: 100%;
  gap: 15px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 20px 0 20px;

`;

export const DetalhesCompras = styled.div`
  gap: 10px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  
  padding: 20px 20px 0 20px;
  position: fixed;
  top: 120px;
  left: 1050px;
  right: 0;
  z-index:1;
  box-shadow: -3px 5px 14px 2px rgba(0,0,0,0.2);
  &>span{
    text-align: left !important;
  }
`;

export const DetalhesContainer = styled.div`
  width: 100%;
  max-width:768px;
  gap: 5px;
  background: #1b1b1b;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
`;

export const PaymentContainerWrapper = styled.div`
  width: 100%;
  min-height: 254px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 20px 15px;
  border-radius: 5px;
  border: #FFF 1px solid;
`;
export const PaymentContainer = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  gap: 5px;
`;

export const DetalhesRow = styled.div`
  width: ${(props) => (props.size ? props.size + "px" : "100%")};
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 10px;
`;

export const DetalhesRow2 = styled.div`
  width: ${(props) => (props.size ? props.size  : "100%")};
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 550px) {
    justify-content: space-evenly;
    gap: 0.3rem;
    margin: 1.5rem 0;
  }
`;

export const DetalhesColumn = styled.div`
  width: ${(props) => (props.size ? props.size : "100%")};
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  border-right: solid rgba(255,255,255, .1) 1px;
  
  @media (max-width: 550px) {
    justify-content: space-evenly;
    gap: 0.3rem;
    margin: 1.5rem 0;
  }
`;

export const Row = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0px;
`;

export const RowIcon = styled.div`
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: flex-start;
  &>span{
    font-size:1rem;
  }
`;

export const ContainerActions = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: ${(props)=>props.direction === 'rtl'? 'row-reverse':'row'};
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
`;

export const Doisinupt = styled.div`
  display: flex;
  margin-left: 9%;
`;
