import React, { useState } from "react";
import SwiperCore, {
  Navigation,
  Pagination,
  Autoplay,
  EffectFade,
  Controller,
} from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

import Zero from "../../assets/Zero.png";
import One from "../../assets/1.png";
import Two from "../../assets/2.png";
import Three from "../../assets/3.png";
import Four from "../../assets/4.png";
import Five from "../../assets/5.png";
import Six from "../../assets/6.png";

import * as S from "./styles";

SwiperCore.use([Navigation, Pagination, Autoplay, EffectFade, Controller]);

const Banner = ({ onSwiper, controller }) => {
  const [firstSwiper, setFirstSwiper] = useState(null);

  const slides = [Zero, One, Two, Three, Four, Five, Six];

  return (
    <S.Container>
      <Swiper
        onSwiper={setFirstSwiper}
        controller={{ control: firstSwiper }}
        id="main"
        tag="section"
        speed={2500}
        effect="flip"
        loop
        spaceBetween={0}
        slidesPerView={1}
        pagination={{ clickable: true }}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        fadeEffect={{
          crossFade: true,
        }}
      >
        {slides.map((item, index) => {
          return (
            <SwiperSlide key={String(index)}>
              <img
                style={{
                  width: "100%",
                  height: "auto",
                  padding: "0px",
                  margin: "0px",
                }}
                src={item}
                allow="autoplay"
                webkitallowfullscreen="true"
                mozallowfullscreen="true"
                title="Banner PalcoWeb"
                alt="Banner PalcoWeb"
              ></img>
            </SwiperSlide>
          );
        })}
      </Swiper>
      {/* <img src="../../assets/banner.png" alt="banner" /> */}

      <S.TitleHome>
        <h1 style={{ color: "#FDCC12" }}>
          PEÇAS DE TEATRO, SHOWS, PALESTRAS TUDO EM 3D e 2D
        </h1>
      </S.TitleHome>
    </S.Container>
  );
};

export default Banner;
