/* eslint-disable no-unused-expressions */
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { useParams } from "react-router-dom";
import { SwiperSlide } from "swiper/react";
import facebook from "../../assets/facebook-yellow.png";
import instagram from "../../assets/instagram-yellow.png";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Card from "../../components/Card";
//share
import Share from "../../components/CardCompartilhar/index";
import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import SpectaclesCarousel from "../../components/SpectaclesCarousel";
import ChannelsCarousel from "../../components/ChannelsCarousel";
import { useShows } from "../../dados/context/ShowsContext";
import Layout from "../../layout";
import * as S from "./styles";

function FichaCanal(props) {
  const {
    showLike,
    emAltaList,
    getAllChannels,
    channel,
    channels,
    channelLike,
    shows,
    showsSignatures,
  } = useShows();

  const { userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";
  const history = useHistory();
  const [compartilharFicha, setcompartilharFicha] = useState(false);
  const [visible, setVisible] = useState(false);
  const [compartilhar, setcompartilhar] = useState(false);
  const params = useParams("/FichaCanal/:id");
  const { id } = params;

  const filteredSpectacles = shows[0]?.spectacle?.filter((spec) => {
    return spec?.producer_id === channel?.id;
  });

  useEffect(() => {
    getAllChannels({ id });
  }, [getAllChannels]);

  return (
    <Layout>
      {
        <>
          <S.Container>
            <S.ContainerImg>
              <S.Thumbnail src={channel?.thumbnailGrande}></S.Thumbnail>
            </S.ContainerImg>

            <S.Conteudo>
              <S.ButtonVoltar
                onClick={() =>
                  history.push("/canais") || window.location.reload()
                }
              >
                <i className="bi bi-x-lg"></i>{" "}
              </S.ButtonVoltar>

              <S.HeaderConteudo>
                <S.Item>
                  {userIsAuthenticated() && (
                    <S.Coracao onClick={() => channelLike(userId, channel.id)}>
                      <i className="bi bi-suit-heart"></i>
                    </S.Coracao>
                  )}

                  <S.Compartilhar>
                    <i
                      className="bi bi-share-fill"
                      onClick={() => setcompartilharFicha(true)}
                      style={{ cursor: "pointer" }}
                    ></i>
                    {compartilharFicha && (
                      <Share
                        link={`http://www.palcoweb.com.br/FichaCanal/${channel?.id}`}
                        sairficha={setcompartilharFicha}
                      ></Share>
                    )}
                  </S.Compartilhar>
                </S.Item>
                <S.Img
                  src={channel?.thumbnail}
                  style={{
                    margin: 0,
                    width: 150,
                    height: 150,
                    borderRadius: "50%",
                    alignItems: "center",
                    lineHeight: 150,
                  }}
                  value={channel?.key}
                  alt="imagem canal"
                />
                <S.Item>
                  <S.HeaderTitle>
                    <S.Span size="1">{channel?.fullName}</S.Span>
                  </S.HeaderTitle>
                  {channel.socialNetwork && (
                    <>
                      <div>
                        {channel?.socialNetwork?.facebook && (
                          <a
                            href={channel?.socialNetwork?.facebook}
                            target="_blank"
                            rel="noopener noreferrer"
                            style={{
                              color: "#fdcc12",
                              textDecorationLine: "none",
                            }}
                          >
                            {" "}
                            <img src={facebook} alt="logo do facebook" />
                          </a>
                        )}
                        {channel?.socialNetwork?.instagram && (
                          <a
                            href={
                              "https://www.instagram.com/" +
                              channel?.socialNetwork?.instagram
                            }
                            target="_blank"
                            rel="noopener noreferrer"
                            style={{
                              color: "#fdcc12",
                              textDecorationLine: "none",
                            }}
                          >
                            <img src={instagram} alt="logo do instagram" />
                          </a>
                        )}
                      </div>
                      {channel?.socialNetwork?.site && (
                        <S.Span
                          cor="#fdcc12"
                          size="1"
                          style={{ cursor: "pointer" }}
                        >
                          <a
                            href={channel?.socialNetwork?.site}
                            target="_blank"
                            rel="noopener noreferrer"
                            style={{
                              color: "#fdcc12",
                              textDecorationLine: "none",
                            }}
                          >
                            Site Oficial
                          </a>
                        </S.Span>
                      )}
                    </>
                  )}
                </S.Item>
              </S.HeaderConteudo>

              <S.Informacoes>
                <S.Item>
                  <S.Text size="0.8">
                    {channel?.description || "Descrição ainda indefinida"}
                  </S.Text>
                </S.Item>
                <S.Item>
                  <S.Span size="0.8">
                    <i className="bi bi-circle-fill p-1"></i>
                    {channel?.category?.label || "-"}
                  </S.Span>
                </S.Item>
              </S.Informacoes>
            </S.Conteudo>
          </S.Container>

          <br />

          {filteredSpectacles && (
            <Section title="Assista também" filter>
              <SpectaclesCarousel
                spectacles={filteredSpectacles}
              ></SpectaclesCarousel>
            </Section>
          )}
        </>
      }
    </Layout>
  );
}

export default FichaCanal;
