import React, { useState, useContext, useEffect } from "react";
import * as S from "./styles";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import { AuthContext } from "../../auth/AuthContext";
import { useHistory } from "react-router-dom";
import firebase from 'firebase/compat/app';
import logoPW from "../../assets/logo-pw.png";
import { cancelPayment } from "../../Connections/apis";
import { useShows } from "../../dados/context/ShowsContext";
import { useAuth } from "../../auth/auth";
import { usePlans } from "../../dados/context/PlansContext";
import "./style.css";

import app from "../../auth/config";

function MeuPerfil() {
  const { getShowsByUser, adquiridos } = useShows();
  const { userIsAuthenticated } = useAuth();
  const [showProfile, setShowProfile] = useState(true);
  const [showPurchases, setShowPuchases] = useState(false);
  const [showRequests, setShowRequests] = useState(false);
  const [historico, setHistorico] = useState([]);
  const history = useHistory();
  const [assinaturaArrayState, setAssinaturaArrayState] = useState([
    {
      id: "24b19ac1-c5cf-4272-872a-fc32358882fb",
      name: "Trimestral",
      value: "$30.90",
      isActive: true,
      is_test_period: false,
      period: null,
    },
    {
      id: "7e0876bb-30d9-458b-a13f-53a3c97274ea",
      name: "Anual",
      value: "$100.00",
      isActive: true,
      is_test_period: false,
      period: null,
    },
    {
      id: "b039e5c0-64a8-43e5-bff8-b9d5112c7c9a",
      name: "Mensal",
      value: "$19.97",
      isActive: true,
      is_test_period: false,
      period: null,
    },
  ]);
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [birthday, setBirthday] = useState("");
  const [phone, setPhone] = useState("");
  // const [onDemandCompras, setOnDemandCompras] = useState([])
  const [signatureCompras, setSignatureCompras] = useState([]);
  const { signature, signatureReport } = useAuth();
  const { getPlan, plans } = usePlans();
  const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";

  useEffect(() => {
    const getUserData = async () => {
      try {
        const res = await app.firestore().collection("users").doc(userId).get();
        setEmail(res.data().email);
        setName(res.data().fullName);
        setBirthday(res.data().birthday);
        setPhone(res.data().phone);
      } catch (err) {}
    };
    getUserData();
  }, []);

  useEffect(() => {
    getShowsByUser();
  }, [userId]);


  return (
    <>
      <Header />

      <S.Container>
        <S.TextoLateral>
          <S.AreaFotoPerfil>
            <S.Foto className="m-2 ">
              {firebase.auth().currentUser?.photoURL === null ? (
                <i
                  style={{ transform: "scale(3)" }}
                  className="bi bi-person-fill"
                ></i>
              ) : (
                <img
                  alt=""
                  style={{
                    width: "50px",
                    height: "50px",
                    borderRadius: "15px",
                  }}
                  src={firebase.auth().currentUser?.photoURL}
                ></img>
              )}
            </S.Foto>
          </S.AreaFotoPerfil>
          <S.SpanLatera
            size="15"
            cor="#FFF"
            className="m-3"
            setadoPerfil={showProfile}
            onClick={() => {
              setShowPuchases(false);
              setShowRequests(false);
              setShowProfile(true);
            }}
          >
            Dados Pessoais
          </S.SpanLatera>
          <S.SpanCompra
            size="15"
            cor="#FFF"
            className="m-3"
            setadoCompra={showPurchases}
            onClick={() => {
              setShowProfile(false);
              setShowRequests(false);
              setShowPuchases(true);
            }}
          >
            Relatorio de compras
          </S.SpanCompra>
          <S.SpanCompra
            size="15"
            cor="#FFF"
            className="m-3"
            setadoCompra={showRequests}
            onClick={() => {
              setShowProfile(false);
              setShowPuchases(false);
              setShowRequests(true);
            }}
          >
            Pedidos
          </S.SpanCompra>
        </S.TextoLateral>

        {showProfile ? (
          <>
            <S.AreaPerfil>
              <S.SpanInput size="23" className=" p-5 ">
                Minha conta
              </S.SpanInput>
              <S.AreaFotoPerfil1>
                <S.Foto className="m-2 ">
                  {firebase.auth().currentUser?.photoURL === null ? (
                    <i
                      style={{ transform: "scale(3)" }}
                      className="bi bi-person-fill"
                    ></i>
                  ) : (
                    <img
                      alt=""
                      style={{
                        width: "50px",
                        height: "50px",
                        borderRadius: "15px",
                      }}
                      src={firebase.auth().currentUser?.photoURL}
                    ></img>
                  )}
                </S.Foto>
              </S.AreaFotoPerfil1>
              <form>
                {/* não tem funcionalidade de atualizar os dados por hora. On submit é na tag form acima */}
                {/* onSubmit={() => dataUpdate} */}
                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Nome Completo
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    id="name"
                    name="name"
                    minLength="4"
                    maxLength="20"
                    value={name}
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Data de nascimento
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    type="date"
                    id="birthday"
                    name="birthday"
                    value={birthday}
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    Celular
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    id="phone"
                    name="phone"
                    minLength="11"
                    maxLength="11"
                    value={phone}
                  ></input>
                </S.GrupoInput>

                <S.GrupoInput className="p-2">
                  <S.SpanInput size="14" className="m-2">
                    E-mail
                  </S.SpanInput>
                  <input
                    disabled
                    className="input"
                    type="text"
                    name="email"
                    id="email"
                    value={email}
                  ></input>
                </S.GrupoInput>
              </form>
            </S.AreaPerfil>
          </>
        ) : (
          //relatorio de compras
          <S.AreaCompras>
            <S.SpanInput size="23" className=" p-5 ">
              Minha conta
            </S.SpanInput>
            <S.Carrinho>
              <S.Info>
                {signature.isActive ? (
                  <>
                    <div
                      style={{
                        display: "flex",
                        height: "150px",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <S.ImgPlano
                        style={{ float: "left" }}
                        src={logoPW}
                      ></S.ImgPlano>
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          marginLeft: "30px",
                        }}
                      >
                        <S.Span size="15">
                          Plano:{" "}
                          <S.Span cor="#FDCC12" size="15">
                            {plans?.map((e) => {
                              return (
                                <>
                                  {e.id === signatureReport?.plan_id ? (
                                    e.name
                                  ) : (
                                    <div></div>
                                  )}
                                </>
                              );
                            })}
                          </S.Span>
                        </S.Span>
                        {/* <S.Span size="15">
                              Data da compra:{" "}
                              {plan?.data
                                .split("/")[1]
                                .concat("/" + plan?.data.split("/")[0])
                                .concat("/" + plan?.data.split("/")[2])}
                            </S.Span> */}
                        <S.Span size="15">
                          Valor do plano:{" "}
                          {plans?.map((e) => {
                            return (
                              <>
                                {e.id === signatureReport?.plan_id ? (
                                  <>
                                    R${e.value.slice(0, -2)},{e.value.slice(-2)}{" "}
                                  </>
                                ) : (
                                  <div></div>
                                )}
                              </>
                            );
                          })}
                        </S.Span>

                        <S.Span size="15">
                          <S.Button
                            onClick={() =>
                              cancelPayment(signatureReport.payment_id)
                            }
                          >
                            Cancelar o Plano
                          </S.Button>
                        </S.Span>
                      </div>
                    </div>

                    <S.BordarBottom></S.BordarBottom>
                    <br />
                  </>
                ) : (
                  <h1 style={{ margin: "auto" }}>Nenhuma assinatura ativa</h1>
                )}

                {adquiridos ? (
                  adquiridos?.map((array) => {
                    return (
                      <>
                        {array.name !== "cardVazio" ? (
                          <div>
                            <div
                              style={{
                                display: "flex",
                                height: "150px",
                                alignItems: "center",
                                width: "100%",
                              }}
                            >
                              <S.Img src={array.thumbnail}></S.Img>
                              <div
                                style={{
                                  display: "flex",
                                  flexDirection: "column",
                                  marginLeft: "30px",
                                }}
                              >
                                <S.Span size="15">
                                  Titulo:
                                  <S.Span cor="#FDCC12" size="15">
                                    {array.title}
                                  </S.Span>
                                </S.Span>
                                <S.Span style={{ display: "flex" }} size="15">
                                  Produção: {array.production}
                                </S.Span>

                                {/* 
                            //Data não está sendo retornada
                            <S.Span size="15">
                              Data da compra:{" "}
                              {array.createdAt
                                .split("/")[1]
                                .concat("/" + array.data.split("/")[0])
                                .concat("/" + array.data.split("/")[2])}
                            </S.Span> */}

                                <S.Span size="15">
                                  Valor da peça: {array.price_on_demand}
                                </S.Span>
                                {/* <S.Span>
                              <S.Button
                                className="cancel-btn"
                              >
                                Cancelar compra
                              </S.Button>
                            </S.Span> */}
                              </div>
                            </div>
                            <S.BordarBottom></S.BordarBottom>
                            <br />
                          </div>
                        ) : (
                          <></>
                        )}
                      </>
                    );
                  })
                ) : (
                  <h1 style={{ margin: "auto" }}>Nenhuma compra realizada</h1>
                )}
              </S.Info>

              <S.CarrinhoItem></S.CarrinhoItem>
            </S.Carrinho>
          </S.AreaCompras>
        )}
      </S.Container>
      <Footer />
    </>
  );
}

export default MeuPerfil;
