import styled from "styled-components";

import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.header`
  position: absolute;
  top: 0;
  z-index: 100;

  width: 100%;
  height: 90px;
  padding: 0px 10px;

  display: flex;
  flex-grow: 1;

  align-items: center;
  justify-content: space-between !important;

  background: #000;
  color: white;
`;

export const CartContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  & i {
    margin-left: 10px;
  }
`;

export const PageToggle = styled.div`
  display: flex;
  width: 100%;
  font-size: 12px;
  font-weight: bold;
  color: ${(props) => (props.yellow ? "#FDCC12" : "#fff")};
  cursor: pointer;
  text-decoration: none;
  justify-content: center;
  transition: all 0.2s;
  // margin: 0;
  padding: 0;

  @media (min-width: 600px) {
    width: 20%;
    float: left;
  }
  @media (min-width: 1600px) {
    font-size: x-large;
  }
`;

export const Icons = styled.div`
  display: flex;
  gap: 0.5vw;

  @media (max-width: ${SCREEN_SIZES.md}) {
    svg {
      display: none;
    }
    display: none;
  }

  input {
    margin-right: -30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    background: transparent;
    border: 1px white solid;
    outline: none;
    color: white;
    border-radius: 5px;
    margin-left: 60px;

    ::-webkit-input-placeholder {
      color: white;
    }
  }

  svg {
    font-size: 25px;
    color: ${({ color, theme }) =>
      color ? `${theme.palette.primary.main}` : "#fff"};
    margin-right: 20px;
    cursor: pointer;

    transition: all 0.2s;

    &:hover {
      color: ${({ theme }) => theme.palette.primary.main};
    }
  }

  svg.search {
    color: white;
    margin-right: 20px;
  }
`;

export const IconsPerfil = styled(Icons)`
  display: flex;
  gap: 0.5vw;

  input {
    margin-right: -30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
    background: transparent;
    border: 1px white solid;
    outline: none;
    color: white;
    border-radius: 5px;
    margin-left: 60px;
  }

  svg {
    font-size: 25px;
    color: ${({ color, theme }) =>
      color ? `${theme.palette.primary.main}` : "#fff"};
    margin-right: 20px;
    cursor: pointer;

    transition: all 0.2s;

    &:hover {
      color: ${({ theme }) => theme.palette.primary.main};
    }
  }

  svg.search {
    color: white;
    margin-right: 20px;
  }
`;

export const Collapse = styled.div`
  display: flex;
  flex-direction: column;
  height: auto;
  color: #fff;
  z-index: 11;
`;

export const ModalMenu = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: center;
  font-size: large;
  gap: 20px;
  position: absolute;
  top: 88px;
  right: 0;
  height: auto;
  width: 60vw;
  z-index: 10;
  background: #222;

  &:first-child {
    padding-top: 10px;
  }

  @media (min-width: 600px) and (max-height: 860px) {
    font-size: large;
    height: 60vh;
    width: 30vw;
  }
  @media (min-width: 800px) and (max-height: 900px) {
    font-size: x-large;
    height: 60vh;
    width: 30vw;
  }
  @media (min-width: 950px) {
    font-size: x-large;
    height: 30vh;
    width: 30vw;
  }
`;

export const DoisBotons = styled.div`
  display: flex;
  flex-direction: row;
  width: 200px;
  justify-content: space-around;
`;

export const Perfil = styled.button`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border-radius: 12px;
  padding: 20px;
  margin: 10px;
  background-color: white;
  border: none;
  color: black;
`;

export const Foto = styled.span`
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const PerfilButton = styled.div`
  position: absolute;
  background-color: white;
  top: 75px;
  min-height: 300px;
  width: 20vw;
  box-shadow: 2px 2px 20px 2px rgba(0, 0, 0, 0.2);
`;

export const Button = styled.button`
  border: transparent;
  background-color: white;
  color: black;
  padding: 8px 10px;
  margin-left: 2vw;
  border-radius: 6px;
  font-weight: bold;
  font-size: small;
  white-space: nowrap;
  transition: all 0.3s;
  &:hover {
    filter: brightness(130%);
  }
  @media (max-width: 400px){
   width: 35%;
   padding: 8px 4px;
  }
`;

export const NomeLogado = styled.button`
  font-size: small;
  color: #fff;
  background: none;
  border: none;
  pointer-events: visible;
  min-width: 80px;
  white-space: nowrap;
`;

export const Div = styled.div`
  
  @media (max-width: 400px){
   width: 100%;
  }
`;