import React, { useRef, useState } from "react";
import * as S from "./styles.js";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import { useHistory, useLocation } from "react-router-dom";
import background from "../../assets/logoPalcoweb.png";
import { useEffect } from "react";

export default function SpectaclesCarousel({ spectacles }) {
  const history = useHistory();

  const slider = useRef(null);

  const [scrollPerClick, setScrollPerClick] = useState(0);
  const [spectaclesSorted, setSpectaclesSorted] = useState([]);

  useEffect(() => {
    const only3D = spectacles?.filter((element) => {
      return element?.title?.includes("3D");
    });

    const onlyPPV = spectacles?.filter((element) => {
      return !element?.title?.includes("3D");
    });

    setSpectaclesSorted([...onlyPPV, ...only3D]);
  }, [spectacles]);

  function handleNext() {
    if (
      scrollPerClick >=
      slider.current.scrollWidth - slider.current.clientWidth
    )
      return;
    setScrollPerClick((prevState) => prevState + 200);
  }
  function handleNextMobile() {
    setScrollPerClick((prevState) => prevState + 200);
  }

  useEffect(() => {
    slider.current.scrollTo({
      top: 0,
      left: scrollPerClick,
      behavior: "smooth",
    });
  }, [scrollPerClick]);

  function handlePrevious() {
    if (scrollPerClick <= 0) return;
    setScrollPerClick((prevState) => prevState - 200);
  }
  function handlePreviousMobile() {
    setScrollPerClick((prevState) => prevState - 200);
  }
  useEffect(() => {
    slider.current.scrollTo({
      top: 0,
      left: scrollPerClick,
      behavior: "smooth",
    });
  }, [scrollPerClick]);

  return (
    <>
      <S.SectionContainer>
        <S.NavPrev
          onClick={handlePrevious}
          onTouchStart={() => handlePreviousMobile}
        >
          <NavigateBeforeIcon style={{ fontSize: 50 }}></NavigateBeforeIcon>
        </S.NavPrev>

        <S.NavNext onClick={handleNext} onTouchStart={() => handleNextMobile}>
          <NavigateNextIcon style={{ fontSize: 50 }}></NavigateNextIcon>
        </S.NavNext>

        <S.ListRow ref={slider}>
          {spectaclesSorted?.length > 0 &&
            spectaclesSorted
              ?.map((item, key) => {
                return (
                  <>
                    {item.name === "cardVazio" ? (
                      <S.ListItem
                        style={{
                          backgroundSize: "contain",
                          backgroundRepeat: "no-repeat",
                        }}
                        src={background}
                        key={key}
                      ></S.ListItem>
                    ) : (
                      <S.ListItem src={item.thumbnail} key={key + 1}>
                        <S.InfoCard>
                          <S.ButtonAcessar
                            className="btn btn-warning"
                            onClick={(i) => {
                              history.push(`/cardezao/${item.id}`);
                            }}
                          >
                            {/* {item.isActiveOnDemand ? "Comprar": "Assinar"} */}
                            Comprar
                          </S.ButtonAcessar>
                        </S.InfoCard>
                      </S.ListItem>
                    )}
                  </>
                );
              })
              .sort(function (a, b) {
                if (!window.location.href.includes("espetaculos")) {
                  if (a?.name?.includes("3d") || b?.name?.includes("3d")) {
                    return 1;
                  }
                  if (!a?.name?.includes("3d") || !b?.name?.includes("3d")) {
                    return -1;
                  }
                  return 0;
                }
              })}
        </S.ListRow>
      </S.SectionContainer>
    </>
  );
}
