import React from "react";

import * as S from "./styles";

function SignatureCard() {
  return (
    <>
      <S.Container>
        <S.SignatureDuration>
          <p>Mensal</p>
        </S.SignatureDuration>
        <S.Hr className="line"></S.Hr>
        <S.SignaturePrice>
          <h2 className="signaturePriceLine">R$15,00</h2>
          <h3 className="signaturePriceNoLine">R$10,00</h3>
        </S.SignaturePrice>
        <S.AssignButton>ASSINAR</S.AssignButton>
      </S.Container>
    </>
  );
}

export default SignatureCard;
