export const cpfMask = (value) => {
  return value
    .replace(/\D/g, "") // substitui qualquer caracter que nao seja numero por nada
    .replace(/(\d{3})(\d)/, "$1.$2") // captura 2 grupos de numero o primeiro de 3 e o segundo de 1, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de numero
    .replace(/(\d{3})(\d)/, "$1.$2")
    .replace(/(\d{3})(\d{1,2})/, "$1-$2")
    .replace(/(-\d{2})\d+?$/, "$1"); // captura 2 numeros seguidos de um traço e não deixa ser digitado mais nada
};

export function convertToNumber(number) {
  return String(number).replace(/\D/g, "");
}
//
export function currency(number, currency = "BRL", locale = "pt-br") {
  return (convertToNumber(number) / 100).toLocaleString(locale, {
    style: "currency",
    currency: currency,
  });
}
export function breakNumber(number) {
  const split = number.split(",");
  const numeric = split[0].split(".");
  const splitByDot = numeric;
  splitByDot.slice(0, 1);
  const integer = numeric[0].replace(/\D/g, "");
  return {
    currency: "R$",
    integer,
    thousand: number.includes(".") ? splitByDot : [],
    decimal: split[1],
  };
}

export const dataMask = (value) => {
  return value
    .replace(/\D/g, "") // substitui qualquer caracter que nao seja numero por nada
    .replace(/(\d{2})(\d)/, "$1/$2") // captura 2 grupos de numero o primeiro de 3 e o segundo de 1, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de numero
    .replace(/(\d{2})(\d)/, "$1/$2");
};

//executa libs de arquivos externos
export const appendScript = (scriptToAppend) => {
  const script = document.createElement("script");
  script.src = scriptToAppend;
  script.async = true;
  document.body.appendChild(script);
};

export const encryptCard = (card, cb) => {
  const { number, exp_month, exp_year, name, security_code } = card;
  const [first_name, ...last_name] = name.split(" ");

  window["Iugu"].setAccountID(process.env.REACT_APP_ACCOUNT_ID_IG);
  // window["Iugu"].setTestMode(true);
  window["Iugu"].setup();

  const cc = window["Iugu"].CreditCard(
    String(number),
    String(exp_month),
    String(exp_year),
    String(first_name),
    last_name.join(" "),
    String(security_code)
  );

  window["Iugu"].createPaymentToken(cc, function (response) {
    if (response.errors) {
      cb && cb(response.errors);
    } else {
      cb && cb(null, response.id);
    }
  });
};

export function extractToArray(arr, keyNumber) {
  let newArr = [];

  if (arr.length < 4) {
    for (let index = 0; index <= 4; index++) {
      newArr.push({ name: "cardVazio" });
    }
  }
  Object.keys(arr).forEach((show) => {
    if (keyNumber) {
      const objExtract = Object.values(arr[show]);
      const keyExtract = Object.keys(arr[show]);
      newArr[keyExtract] = objExtract;
    } else {
      const objExtract = Object.values(arr[show])[0];
      newArr.unshift(objExtract);
    }
  });
  return newArr;
}

export function isValidCpf(strCPF) {
  var Soma;
  var Resto;
  Soma = 0;
  //strCPF  = RetiraCaracteresInvalidos(strCPF,11);
  if (strCPF.length < 11) {
    return false;
  }
  if (strCPF === "00000000000") return false;
  for (var i = 1; i <= 9; i++)
    Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (11 - i);
  Resto = (Soma * 10) % 11;
  if (Resto === 10 || Resto === 11) Resto = 0;
  if (Resto !== parseInt(strCPF.substring(9, 10))) return false;
  Soma = 0;
  for (var i = 1; i <= 10; i++)
    Soma = Soma + parseInt(strCPF.substring(i - 1, i)) * (12 - i);
  Resto = (Soma * 10) % 11;
  if (Resto === 10 || Resto === 11) Resto = 0;
  if (Resto !== parseInt(strCPF.substring(10, 11))) return false;
  return true;
}
