import axios from 'axios';
import { iuguUrl } from './consts';

const apiIg = axios.create({
    baseURL: iuguUrl,
});

apiIg.interceptors.request.use(config => {
    if (process.env.REACT_APP_PAYMENT_TOKEN_IG) {
        config.headers.Authorization = `Bearer ${process.env.REACT_APP_PAYMENT_TOKEN_IG}`;
        config.headers["Access-Control-Allow-Origin"] = process.env.REACT_APP_URL_APP_IG;
        config.headers["Content-Type"] = "application/json";
    }

    return config;
});



export default apiIg;