import moment from "moment";
import Select from "react-select";
import React, { useEffect, useState } from "react";
import { AiOutlineCreditCard } from "react-icons/ai";
import { BsXDiamond } from "react-icons/bs";
import { FaBarcode } from "react-icons/fa";
import { GoCreditCard } from "react-icons/go";
import { useHistory } from "react-router";
import { toast, ToastContainer } from "react-toastify";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Footer from "../../components/Footer";
import GetAddressByCep from "../../components/GetAddressByCep";
import Header from "../../components/Header";
import { useCarts } from "../../dados/context/CartContext";
import { usePayments } from "../../dados/context/PaymentContext";
import { optionsToast } from "../../dados/context/ShowsContext";
import { GlobalStyleSelect } from "../../theme/global";
import { convertToNumber, currency } from "../../utils/utils.js";
import "./style.css";
import * as S from "./styles";

function Pagamento(props) {
  const {
    paymentList,
    paySignature,
    paySpectacle,
    validate,
    removeToPayment,
    error,
  } = usePayments();
  const { updateSignature, setRouteToStack } = useAuth();
  const { removeToCart } = useCarts();
  const [nome, setNome] = useState("");
  const [cpf, setCpf] = useState("");
  const [cvv, setcvv] = useState("");
  const [data, setdata] = useState("");
  const [months, setMonths] = useState("");
  const [phone_prefix, setPhone_prefix] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [errors, setErrors] = useState([]);
  const [address, setAddress] = useState("");
  const [cep, setCep] = useState("");
  const [number, setNumber] = useState("");
  const [complement, setComplement] = useState("");
  const [loading, setLoading] = useState(false);
  const [isCredit, setIsCredit] = useState(true);
  const [isDebit, setIsDebit] = useState(false);
  const [isBarCode, setIsBarCode] = useState(false);
  const [isPix, setIsPix] = useState(false);
  const [step, setStep] = useState("address");

  const history = useHistory();

  const userId = app.auth().currentUser?.uid;

  const isSignature = paymentList.signature_id ? true : false;

  const getCurrentAddress = () => {
    app
      .firestore()
      .collection("users")
      .doc(userId)
      .get()
      .then((snapshot) => {
        const currentAddress = snapshot.data()?.address;

        if (currentAddress) {
          setAddress(currentAddress?.fullAddress);
          setCep(currentAddress?.cep);
          setNumber(currentAddress?.number);
          setComplement(currentAddress?.complement);
        }
      });
  };

  const handleSubmitAddress = async (e) => {
    e.preventDefault();
    setStep("payment");
    const dataAddress = {
      address: address,
      cep: cep,
      number: number,
      complement: complement,
    };

    //VERIFICAR SE O CÓDIGO ABAIXO ESTÁ CORRETO
    //ADICIONA O CAMPO user_address NO OBJ DO USUÁRIO

    app.firestore().collection("users").doc(userId).set(
      {
        user_address: dataAddress,
      },
      { merge: true }
    );
  };

  const monthsOptions = [
    { value: 1, label: "1x" },
    { value: 2, label: "2x" },
    { value: 3, label: "3x" },
    { value: 4, label: "4x" },
    { value: 5, label: "5x" },
    { value: 6, label: "6x" },
    { value: 7, label: "7x" },
    { value: 8, label: "8x" },
    { value: 9, label: "9x" },
    { value: 10, label: "10x" },
    { value: 11, label: "11x" },
    { value: 12, label: "12x" },
  ];

  const generateOptions = () => {
    const value =
      convertToNumber(paymentList.signature_value) ||
      convertToNumber(paymentList.valorPreco);
    let limit = 0;
    for (let i = 1; i <= 12; i++) {
      if (value / 100 / i >= 5) {
        limit = i;
      }
    }
    const months = monthsOptions.filter((m) => m.value <= limit);
    return months.map((m) => ({
      ...m,
      label: `${m.value}x de R$${(value / 100 / m.value).toFixed(2)}`,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { nome, cvv, cardNumber, data, cpf, phone_prefix, phone, email } =
      e.target.elements;
    const [street, district, city, state] = address.split(",");
    const [month, year] = data.value.split("/");
    const addressFull = {
      street,
      number: number,
      district,
      city,
      state,
      zip_code: cep,
      complement,
    };
    if (isDisabled()) {
      toast.warning("verifique os campos inválidos", optionsToast);
      return;
    }

    const userId = app.auth().currentUser?.uid;
    const itemId = paymentList.id;
    const value =
      convertToNumber(paymentList.signature_value) ||
      convertToNumber(paymentList.valorPreco);

    const dataPayment = {
      userId,
      itemId,
      name: nome.value,
      phone_prefix: convertToNumber(phone_prefix.value),
      phone: convertToNumber(phone.value),
      email: email.value,
      months: months ? months.value : 1,
      price_cents: Number(value),
      number: convertToNumber(cardNumber.value),
      exp_month: month,
      exp_year: `20${year}`,
      security_code: cvv.value,
      address: addressFull,
    };
    if (isSignature) {
      setLoading(true);
      paySignature(dataPayment, (resp, error) => {
        if (resp) {
          updateSignature(true);
          removeToCart();
          removeToPayment();
          setRouteToStack("/pagamento");
          toast.success("Pagamento feito com sucesso", optionsToast);
          setLoading(false);
          setMonths('');
          history.push(`/ConfirmCompra/${paymentList.id}`);
        } else if (error) {
          toast.error("Ooops, algo deu errado", optionsToast);
          setMonths('');
          setLoading(false);
        }
      });
    } else {
      //spectacle
      setLoading(true);
      paySpectacle(dataPayment, (resp, error) => {
        if (resp) {
          removeToCart();
          removeToPayment();
          setRouteToStack("/pagamento");
          toast.success("Pagamento feito com sucesso", optionsToast);
          setLoading(false);
          setMonths('');
          history.push(`/ConfirmCompra/${paymentList.id}`);
        } else if (error) {
          //Tratar Erro no cb
          toast.error("Ooops, algo deu errado", optionsToast);
          setMonths('');
          setLoading(false);
        }
      });
    }
  };
  useEffect(() => {
    isDisabled();

    getCurrentAddress();
  }, [error, getCurrentAddress, isDisabled]);

  function isDisabled() {
    const fields = ["nome", "cpf"];
    if (isFillFields()) {
      return fields.some((field) =>
        error[field] !== "isValid" ? true : false
      );
    } else {
      return true;
    }
  }

  function isFillFields() {
    return Object.keys(error).length > 0 ? true : false;
  }

  const CreditMethodBackground = isCredit ? "#fdcc12" : "fff";
  const DebtMethodBackground = isDebit ? "#fdcc12" : "fff";
  const BarCodeBackground = isBarCode ? "#fdcc12" : "fff";
  const PixBackground = isPix ? "#fdcc12" : "fff";

  const AllPaymentMethod = [isCredit, isDebit, isBarCode, isPix];
  return (
    <>
      <Header />
      <ToastContainer />
      <S.Container>
        <S.Detalhes>
          <S.Span size="1.5" bold="bold">
            Pagamento
          </S.Span>
        </S.Detalhes>
        <S.DetalhesCompras>
          <S.Span size="1" bold="bold">
            Detalhes da compra
          </S.Span>
          <S.DetalhesRow>
            <S.Span size=".8">Valor:</S.Span>
            <S.Span size="1.2">
              {(paymentList.value && currency(paymentList.value)) ||
                (paymentList.valorPreco && currency(paymentList.valorPreco))}
            </S.Span>
          </S.DetalhesRow>

          <div
            style={{
              border: "dashed rgba(255,255,255,.2) 1px",
              width: "100%",
            }}
          ></div>
        </S.DetalhesCompras>

        {AllPaymentMethod.every((element) => element === false) && (
          <S.ButtonVoltar onClick={() => history.goBack()}>
            <i className="bi bi-chevron-left"></i>Voltar
          </S.ButtonVoltar>
        )}
        {step === "address" && (
          <S.Form onSubmit={(e) => handleSubmitAddress(e)}>
            <S.DetalhesContainer>
              <S.Span size="1.2" bold={"bold"}>
                Endereço
              </S.Span>
            </S.DetalhesContainer>
            <S.DetalhesContainer>
              <S.Span size={".8"} cor={"rgba(255,255,255,.2)"}>
                *Preencha os dados do endereço e siga com o pagamento :D
              </S.Span>
            </S.DetalhesContainer>

            <S.Row>
              <GetAddressByCep
                setAddress={setAddress}
                address={address}
                setCep={setCep}
                cep={cep}
              ></GetAddressByCep>
            </S.Row>

            <S.Row>
              <div className="group">
                <S.Input
                  className="form-control"
                  placeholder="Rua..."
                  value={address}
                  onChange={(e) => setAddress(e.target.value)}
                  size={420}
                />
              </div>
              <div className="group">
                <S.Input
                  className="form-control"
                  placeholder="Número"
                  value={number}
                  onChange={(e) => setNumber(e.target.value)}
                  size={100}
                />
              </div>
            </S.Row>

            <S.Row>
              <div className="group">
                <S.Input
                  className="form-control"
                  placeholder="Complemento"
                  value={complement}
                  onChange={(e) => setComplement(e.target.value)}
                />
              </div>
            </S.Row>

            <S.ContainerActions direction="rtl">
              <S.Button type="submit" disabled={isDisabled}>
                {!loading ? "Próximo" : "..."}
              </S.Button>
            </S.ContainerActions>
          </S.Form>
        )}
        {step === "payment" && (
          <S.Form onSubmit={(e) => handleSubmit(e)}>
            <S.DetalhesContainer>
              <S.Span size="1.2" bold={"bold"}>
                Dados do Cliente
              </S.Span>
            </S.DetalhesContainer>
            <S.DetalhesContainer>
              <S.Span size={".8"} cor={"rgba(255,255,255,.2)"}>
                *Preencha os dados do cliente e efetue o pagamento :D
              </S.Span>
            </S.DetalhesContainer>
            <S.Row>
              <div className="group position-relative">
                <S.Input
                  className="form-control"
                  placeholder="Nome"
                  name="nome"
                  onBlur={(e) => validate(e, { required: true, min: 3 })}
                />
                <div
                  className={`tooltip-error ${error["nome"] && error["nome"] !== "isValid" ? "show" : ""
                    }`}
                >
                  {error["nome"]}
                </div>
              </div>
            </S.Row>
            <S.Row>
              <div className="group position-relative">
                <S.Input
                  className="form-control"
                  placeholder="Email"
                  name="email"
                  onBlur={(e) => validate(e, { required: true, min: 3 })}
                />
                <div
                  className={`tooltip-error ${error["email"] && error["email"] !== "isValid" ? "show" : ""
                    }`}
                >
                  {error["email"]}
                </div>
              </div>
            </S.Row>

            <S.Row>
              <div className="group position-relative">
                <S.Input
                  size={150}
                  className="form-control"
                  placeholder="DDD"
                  onChange={(e) => setNumber(e)}
                  mask="999"
                  maskChar={null}
                  name="phone_prefix"
                  onBlur={(e) => validate(e, { required: true, min: 2 })}
                />
                <div
                  className={`tooltip-error ${error["phone_prefix"] && error["phone_prefix"] !== "isValid"
                    ? "show"
                    : ""
                    }`}
                >
                  {error["phone_prefix"]}
                </div>
              </div>
              <div className="group position-relative">
                <S.Input
                  size={530}
                  className="form-control"
                  placeholder="Telefone"
                  onChange={(e) => setCpf(e)}
                  mask="9 9999-9999"
                  maskChar={null}
                  name="phone"
                  onBlur={(e) => validate(e, { required: true })}
                />
                <div
                  className={`tooltip-error ${error["phone"] && error["phone"] !== "isValid" ? "show" : ""
                    }`}
                >
                  {error["phone"]}
                </div>
              </div>
            </S.Row>

            <S.PaymentContainerWrapper>
              <S.DetalhesContainer>
                <S.Span size={"1"} cor={"rgba(255,255,255,.8)"} bold={"normal"}>
                  Escolha um Meio de Pagamento
                </S.Span>
              </S.DetalhesContainer>
              <S.PaymentContainer>
                <S.DetalhesColumn size={"15rem"}>
                  <S.Span2
                    size={1}
                    cor={CreditMethodBackground}
                    onClick={() => {
                      setIsCredit(true);
                      setIsDebit(false);
                      setIsBarCode(false);
                      setIsPix(false);
                    }}
                  >
                    <AiOutlineCreditCard size={"1.5rem"} />
                    Cartão de Crédito
                  </S.Span2>
                  {/* <S.Span2
                    cor={DebtMethodBackground}
                    disabled={true}
                    // onClick={() => {
                    //   setIsCredit(false);
                    //   setIsDebit(true);
                    //   setIsBarCode(false);
                    //   setIsPix(false);
                    // }}
                  >
                    <GoCreditCard size={"1.5rem"} />
                    Débito
                  </S.Span2>
                  <S.Span2
                    cor={BarCodeBackground}
                    disabled={true}
                    // onClick={() => {
                    //   setIsCredit(false);
                    //   setIsDebit(false);
                    //   setIsBarCode(true);
                    //   setIsPix(false);
                    // }}
                  >
                    <FaBarcode size={"1.5rem"} />
                    Boleto Bancário
                  </S.Span2>
                  <S.Span2
                    cor={PixBackground}
                    disabled={true}
                    // onClick={() => {
                    //   setIsCredit(false);
                    //   setIsDebit(false);
                    //   setIsBarCode(false);
                    //   setIsPix(true);
                    // }}
                  >
                    <BsXDiamond size={"1.5rem"} />
                    Pix
                  </S.Span2> */}
                </S.DetalhesColumn>
                {(isCredit || isDebit) && (
                  <S.DetalhesRow2>
                    <S.Row>
                      <div class="group position-relative">
                        <S.Input
                          className="form-control"
                          placeholder="Número do Cartão"
                          name="cardNumber"
                          mask="9999 9999 9999 9999 999"
                          maskChar={null}
                          onBlur={(e) =>
                            validate(e, { required: true, cardNumber: true })
                          }
                        ></S.Input>
                        <div
                          className={`tooltip-error ${error["cardNumber"] &&
                            error["cardNumber"] !== "isValid"
                            ? "show"
                            : ""
                            }`}
                        >
                          {error["cardNumber"]}
                        </div>
                      </div>
                    </S.Row>
                    <S.Row>
                      <div class="group position-relative">
                        <S.Input
                          className="form-control"
                          placeholder="CVV"
                          name="cvv"
                          maxLength={4}
                          onBlur={(e) =>
                            validate(e, { required: true, cvv: true })
                          }
                        ></S.Input>
                        <div
                          className={`tooltip-error ${error["cvv"] && error["cvv"] !== "isValid"
                            ? "show"
                            : ""
                            }`}
                        >
                          {error["cvv"]}
                        </div>
                      </div>
                      <div class="group position-relative">
                        <S.Input
                          className="form-control"
                          type="data"
                          name="data"
                          placeholder="validade"
                          mask="99/99"
                          maskChar={null}
                          onBlur={(e) =>
                            validate(e, { required: true, expDate: true })
                          }
                        ></S.Input>
                        <div
                          className={`tooltip-error ${error["data"] && error["data"] !== "isValid"
                            ? "show"
                            : ""
                            }`}
                        >
                          {error["data"]}
                        </div>
                      </div>
                    </S.Row>
                    <S.Row>
                      <div class="group position-relative">
                        <Select
                          id="months"
                          name="months"
                          type="text"
                          placeholder="Selecione uma opção de parcelamento"
                          className="react-select"
                          classNamePrefix="react-select"
                          styles={GlobalStyleSelect}
                          value={months && months}
                          options={generateOptions()}
                          onChange={(data) => setMonths(data)}
                        />
                      </div>
                    </S.Row>
                  </S.DetalhesRow2>
                )}
              </S.PaymentContainer>
            </S.PaymentContainerWrapper>

            <S.ContainerActions>
              <S.ButtonVoltar onClick={() => setStep("address")}>
                <i className="bi bi-chevron-left"></i>Voltar
              </S.ButtonVoltar>
              <S.Button type="submit" disabled={isDisabled && loading}>
                {!loading ? "PAGAR" : "..."}
              </S.Button>
            </S.ContainerActions>
          </S.Form>
        )}
      </S.Container>
      <Footer />
    </>
  );
}

export default Pagamento;
