import React, { useEffect } from "react";
import { useHistory } from "react-router";
import { SwiperSlide } from "swiper/react";
import { useAuth } from "../../auth/auth";
import ButtonContinuar from "../../components/ButtonContinuar";
import Card from "../../components/Card";
import * as SCard from "../Canais/styles";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import * as S from "./styles";
import app from "../../auth/config";
import background from "../../assets/logoPalcoweb.png";
import SpectaclesCarousel from "../../components/SpectaclesCarousel";
import ChannelsCarousel from "../../components/ChannelsCarousel";

function Espetaculos() {
  const {
    adquiridos,
    espetaculosFavoritos,
    getShowsByUser,
    getShowsByUserFav,
    getAllChannelsFavoriteByUser,
    channelFavoritesDeep,
  } = useShows();
  const { signature, userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";
  const history = useHistory();

  const channelEmptyArray = ["", "", "", "", ""];

  useEffect(() => {
    getShowsByUser();
  }, []);

  useEffect(() => {
    getShowsByUserFav();
  }, []);

  useEffect(() => {
    userId && getAllChannelsFavoriteByUser(userId, true);
  }, [userId]);

  return (
    <>
      <Header />

      <S.Container>
        <h2>Meus Espetáculos</h2>

        <Section title="Adquiridos" filter>
          <SpectaclesCarousel spectacles={adquiridos} />
        </Section>

        <Section title="Favoritos" filter>
          <SpectaclesCarousel spectacles={espetaculosFavoritos} />
        </Section>

        <Section title="Canais Favoritos" filter>
          <ChannelsCarousel spectacles={channelFavoritesDeep} />
        </Section>
      </S.Container>
      <Footer />
    </>
  );
}

export default Espetaculos;
