import React, { useState } from "react";
import * as S from "./styles";
import house from "../../assets/house.png";

import { useHistory } from "react-router-dom";
import firebase from 'firebase/compat/app';
import { useAuth } from "../../auth/auth";

function Header({ page, setPoupUp }) {
  const { userSigned, userIsAuthenticated, signOut } = useAuth();
  const [collapse, setCollapse] = useState(false);

  const history = useHistory();

  window.addEventListener("click", function (event) {
    if (collapse === true) {
      setTimeout(() => setCollapse(false), "5000");
    }
  });

  if (userIsAuthenticated()) {
    return (
      <S.Container>
        <S.Div>
          <img
            src={house}
            alt="Logotipo Palcoweb"
            onClick={() => history.push("/")}
          />
          <S.Button
            onClick={() => history.push(`/categorias`)}
          >
            Espetáculos
          </S.Button>
          <S.Button
            onClick={() => history.push(`/saibaMais`)}
          >
            Saiba mais
          </S.Button>
        </S.Div>

        <S.CartContainer>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              textAlign: "center",
              alignItems: "center",
            }}
          >
            {userSigned && firebase.auth().currentUser?.photoURL == null ? (
              <i
                style={{ fontSize: "30px" }}
                className="bi bi-person-fill"
                onClick={() => {
                  collapse === false ? setCollapse(true) : setCollapse(false);
                }}
              ></i>
            ) : (
              <img
                onClick={() => {
                  collapse === false ? setCollapse(true) : setCollapse(false);
                }}
                src={firebase.auth().currentUser?.photoURL}
                style={{ width: "40px", height: "40px", borderRadius: "15px" }}
                alt="Profile"
                referrerPolicy="no-referrer"
              ></img>
            )}
          </div>
          <i
            className="bi bi-cart3"
            style={{ fontSize: "30px" }}
            onClick={() => history.push("/cart")}
          ></i>
        </S.CartContainer>
        {collapse ? (
          <S.Collapse>
            <S.ModalMenu>
              <div onClick={() => history.push("/espetaculos/assinante")}>
                Assinatura
              </div>
              <div
                onClick={() =>
                  userSigned &&
                  userSigned.profileId ===
                    "2ad42cbc-b42f-49dc-92db-a61941e67706"
                    ? history.push("/espetaculos")
                    : history.push("/espetaculos/mkt")
                }
              >
                Meus Espetáculos
              </div>
              <div onClick={() => history.push("/perfil")}>Minha Conta</div>
              <div
                onClick={() => {
                  signOut();
                  history.push("/");
                }}
              >
                Sair
              </div>
            </S.ModalMenu>
          </S.Collapse>
        ) : null}
      </S.Container>
    );
  } else {
    return (
      <S.Container>
        <S.Div>
          <img
            src={house}
            alt="Logotipo Palcoweb"
            onClick={() => history.push("/")}
          />
          <S.Button
            onClick={() => history.push(`/categorias`)}
          >
            Espetáculos
          </S.Button>
          <S.Button
            onClick={() => history.push(`/saibaMais`)}
          >
            Saiba mais
          </S.Button>
        </S.Div>

        <S.CartContainer>
          <button
            style={{
              color: "black",
              borderRadius: "6px",
              border: "transparent",
              marginLeft: "2vw",
              padding: "8px 10px",
              fontWeight: "bold",
              fontSize: "large",
            }}
            onClick={() => history.push(`/login`)}
          >
            Entrar
          </button>

          <i
            className="bi bi-cart3"
            style={{ fontSize: "30px" }}
            onClick={() => history.push("/cart")}
          ></i>
        </S.CartContainer>
      </S.Container>
    );
  }
}

export default Header;
