import React, { useEffect, useState, createContext, useContext } from "react";
import app from "../../auth/config";
import ShowsService from "../../Connections/shows";
import ChannelUtils from "../../Connections/channel";
import { extractToArray } from "../../utils/utils";

const Shows = createContext({});

export function ShowsProvider(props) {
  const [shows, setShows] = useState([]);
  const [channels, setChannels] = useState([]);
  const [channelFavorites, setChannelFavorites] = useState([]);
  const [channelFavoritesDeep, setChannelFavoritesDeep] = useState([]);
  const [viewFavoritesDeep, setViewFavoritesDeep] = useState([]);
  const [channel, setChannel] = useState({});
  const [lancamentos, setLancamentos] = useState([]);
  const [emAltaList, setEmAlta] = useState([]);
  const [adquiridos, setAdquiridos] = useState([]);
  const [espetaculosFavoritos, setEspetaculosFavoritos] = useState([]);
  const [showsSignatures, setShowsSignatures] = useState([]);
  const [show, setShow] = useState({});
  const [showWithLink, setShowWithLink] = useState({});
  const [status, setStatus] = useState({});
  const [hasBeenWatched, sethasBeenWatched] = useState([]);
  const [showsByCategory, setShowsByCategory] = useState([]);
  const [categories, setCategories] = useState([]);
  const [subCategories, setSubCategories] = useState([]);

  useEffect(() => {
    getShow();
  }, []);

  useEffect(() => {
    getShowsOnDemand(true);
  }, []);

  useEffect(() => {
    getShowsIsTrending(true);
  }, []);

  useEffect(() => {
    getShowsSignature(true);
  }, []);

  const isInFavorites = (id) => {
    if (espetaculosFavoritos.find((fav) => fav.id === id)) {
      return true;
    } else return false;
  };

  const isInFavoritesChannel = (id) => {
    if (channelFavorites.find((fav) => fav.channel_id === id)) {
      return true;
    } else return false;
  };

  const channelIsInFavorites = (user_id, channel_id, cb) => {
    // if (channelFavorites.find((fav) => fav.channel_id === channel_id)) {
    //   return true;
    // } else return false;
    // query in firebase by channel id

    let hasChannel = false;

    app
      .firestore()
      .collection("users")
      .doc(user_id)
      .get()
      .then((doc) => {
        const user = { ...doc.data() };
        hasChannel =
          user.channels &&
          user.channels.find((el) => el.channel_id === channel_id)
            ? true
            : false;
        if (cb) {
          cb(hasChannel);
        }

        if (user.channels.find((el) => el.channel_id === channel_id)) {
          return user.channels &&
            user.channels.find((el) => el.channel_id === channel_id)
            ? true
            : false;
        }
      })
      .catch((error) => {});
  };

  const alreadyWatched = (user_id, spectacle_id) => {
    //query in firebase by channel id
    app
      .firestore()
      .collection("users")
      .doc(user_id)
      .get()
      .then((doc) => {
        const user = { ...doc.data() };
        const hasWatched = user.views;
        sethasBeenWatched(hasWatched);
      })
      .catch((error) => {});
  };

  const getShow = async () => {
    try {
      const response = await ShowsService.loadShows();
      setShows(response.data?.body);
    } catch (error) {}
  };

  const getShowById = async (id) => {
    try {
      const response = await ShowsService.getShowById(id);
      setShow(response.data?.body);
    } catch (error) {}
  };

  const getShowWithLink = async (id, cb) => {
    try {
      const response = await ShowsService.getShowLink(id);
      cb && cb(null, response.data?.body);
      setShowWithLink(response.data?.body);
    } catch (error) {
      cb && cb(error.response, null);
    }
  };

  const getShowsByUser = async () => {
    try {
      const response = await ShowsService.getShowsByUser();
      const data = extractToArray(response.data?.body);
      setAdquiridos(data);
    } catch (error) {}
  };

  const getShowsByUserFav = async () => {
    try {
      const response = await ShowsService.getShowsByUserFav();
      const data = extractToArray(response.data?.body);
      setEspetaculosFavoritos(data);
    } catch (error) {}
  };

  const getShowsByCategory = async (value) => {
    try {
      const response = await ShowsService.getShowsByCategory(value);
      setShowsByCategory(response.data?.body);
      return response.data?.body;
    } catch (error) {}
  };

  const getCategories = async () => {
    try {
      const response = await ShowsService.getCategories();
      setCategories(response.data?.body);
    } catch (error) {}
  };

  const getSubCategories = async (value) => {
    try {
      const response = await ShowsService.getSubCategories(value);
      setSubCategories(response.data?.body);
    } catch (error) {}
  };

  const getShowsOnDemand = async (value) => {
    try {
      const response = await ShowsService.getShowsOnDemand(value);
      setLancamentos(response.data?.body);
    } catch (error) {}
  };

  const getShowsSignature = async (value) => {
    try {
      const response = await ShowsService.getShowsSignature(value);
      setShowsSignatures(response.data?.body[0]);
    } catch (error) {}
  };

  const getShowsIsTrending = async (value) => {
    try {
      const response = await ShowsService.getShowsTrending(value);
      setEmAlta(response.data?.body);
    } catch (error) {}
  };

  const getAllChannels = async (filter) => {
    if (filter) {
      app
        .firestore()
        .collection("users")
        .doc(filter.id)
        .get()
        .then((doc) => {
          setChannel({ id: doc.id, ...doc.data() });
        })
        .catch((error) => {});
      return;
    }
    app
      .firestore()
      .collection("users")
      .where("profileId", "==", "9421e035-1fd6-4918-8fc7-f4e865996bbc")
      .get()
      .then((snap) => {
        let arrChannels = [];
        snap.forEach((doc) => {
          arrChannels.push({ id: doc.id, ...doc.data() });
        });
        arrChannels = arrChannels.filter((doc) => {
          return doc?.isDeactivated !== 2;
        });
        setChannels(arrChannels);
      })
      .catch((error) => {});
    return;
  };

  const getAllChannelsFavoriteByUser = (user_id, deep) => {
    let channels = [];

    app
      .firestore()
      .collection("users")
      .where("profileId", "==", "9421e035-1fd6-4918-8fc7-f4e865996bbc")
      .get()
      .then((snap) => {
        snap.forEach((doc) => {
          channels.push({ id: doc.id, ...doc.data() });
        });
        // setChannels(arrChannels);
      })
      .catch((error) => {});

    if (channels && deep) {
      app
        .firestore()
        .collection("users")
        .doc(user_id)
        .get()
        .then((doc) => {
          const user = { ...doc.data() };
          let arrchannels = [];
          user.channels &&
            user.channels.forEach((channel) => {
              arrchannels.push(
                channels.find((el) => el.id === channel.channel_id)
              );
            });
          setChannelFavoritesDeep(arrchannels);
        })
        .catch((error) => {});
      return;
    } else if (!deep) {
      app
        .firestore()
        .collection("users")
        .doc(user_id)
        .get()
        .then((doc) => {
          const user = { ...doc.data() };
          let arrchannels = [];
          user.channels &&
            user.channels.forEach((channel) => {
              arrchannels.push(channel);
            });
          setChannelFavorites(arrchannels);
        })
        .catch((error) => {});
      return;
    }
    return;
  };

  const showLike = async (user_id, spectacle_id) => {
    if (!isInFavorites(spectacle_id)) {
      try {
        await ShowsService.setShowLike(user_id, spectacle_id);
        getShowsByUserFav();
        setStatus(true);
      } catch (error) {}
    } else {
      showUnLike(user_id, spectacle_id);
    }
    return;
  };

  const getAllViews = (user_id, spectacle_id) => {
    //pega as info na collection visualizações e compara com a id da peça. Depois aumenta o contador em 1 de visualização da peça.

    //  app.firestore().collection("visualizacoes").doc(spectacle_id).update({

    // })
    let visualizacoes = [];

    app
      .firestore()
      .collection("visualizacoes")
      .doc(spectacle_id)
      .get()
      .then((snapshot) => {
        const procuraEspetaculo = snapshot.exists;

        if (!procuraEspetaculo) {
          app
            .firestore()
            .collection("visualizacoes")
            .doc(spectacle_id)
            .set({
              visualizacoes: [
                {
                  data: new Date().toLocaleDateString("pt-br", "day"),
                  id: spectacle_id,
                  views: 1,
                },
              ],
            })
            .then((res) => {
              app
                .firestore()
                .collection("visualizacoes")
                .doc(spectacle_id)
                .update({
                  visualizacoes: [
                    {
                      data: new Date().toLocaleDateString("pt-br", "day"),
                      id: spectacle_id,
                      views: 1,
                    },
                  ],
                });
            });
        } else {
          const procuraViewsNoDia = snapshot
            .data()
            .visualizacoes.find(
              (day) => day.data == new Date().toLocaleDateString("pt-br", "day")
            );

          if (!procuraViewsNoDia) {
            visualizacoes.push({
              data: new Date().toLocaleDateString("pt-br", "day"),
              id: spectacle_id,
              views: 1,
            });
          } else {
            snapshot.data().visualizacoes.forEach((doc) => {
              let views = 0;

              if (doc.data === new Date().toLocaleDateString("pt-br", "day")) {
                views = doc.views + 1;
              }
              visualizacoes.push({ ...doc, views });
            });
          }
        }
        app.firestore().collection("visualizacoes").doc(spectacle_id).update({
          visualizacoes: visualizacoes,
          //     views: doc.data().views + 1
        });
        //   if (doc.id.includes(new Date().toLocaleDateString( 'pt-br', {month: 'long'}))) {

        //     if(doc.id.spectacle_id) {} else{}
        //   }
        // doc.data().forEach(d =>{
        //   if (d.includes(spectacle_id)) {
        //     app.firestore().collection("visualizacoes").doc(d.id).update({

        //     views: doc.data().views + 1,
        //     showId: spectacle_id,
        //     date: new Date().toLocaleDateString('pt-br', { dateStyle: 'short' })
        //     })
        //   }
        // })

        // if (!doc.data().id.includes(new Date().toLocaleDateString( 'pt-br', {month: 'long'}))) {

        //   app.firestore().collection("visualizacoes").doc(new Date().toLocaleDateString( 'pt-br', 'month').i).set({
        //     date:"formato pre definido",
        //     show:"id da peça",
        //     views:101
        //   })
        // }

        // if (doc.data().show == getShowById(doc.data().show)) {

        //   app.firestore().collection("visualizacoes").doc(doc.id).update({

        //     views: doc.data().views + 1
        //   })
        // }

        //   }
      });
    // })

    // app.firestore().collection("visualizacoes").get().then((snapshot) => {
    //   snapshot.docs.forEach(doc => {
    //   })
    // })

    // app
    //   .firestore()
    //   .collection("users")
    //   .where("profileId", "==", user_id)
    //   .get()
    //   .then((snap) => {
    //     snap.forEach((doc) => {
    //       views.push({ id: doc.id, ...doc.data() });
    //     });
    //     // setChannels(arrChannels);
    //   })
    //   .catch((error) => {});
    // if (views) {
    //   app
    //     .firestore()
    //     .collection("users")
    //     .doc(user_id)
    //     .get()
    //     .then((doc) => {
    //       const user = { ...doc.data() };
    //       let arrViews = [];
    //       user.channels.forEach((show) => {
    //         arrViews.push(views.find((el) => el.id === show.spectacle_id));
    //       });
    //       setViewFavoritesDeep(arrViews);
    //     })
    //     .catch((error) => {});
    //   return;
    // }
    return;
  };

  const watchShow = async (user_id, spectacle_id, isOnDemand) => {
    const now = new Date();

    getAllViews(user_id, spectacle_id);
    if (!alreadyWatched(user_id, spectacle_id)) {
      app
        .firestore()
        .collection("users")
        .doc(user_id)
        .set(
          {
            views: [
              ...viewFavoritesDeep,
              {
                user_id,
                show: spectacle_id,
                isOnDemand: isOnDemand || false,
                date: `${now.getFullYear()}/${now.getMonth()}`,
              },
            ],
          },
          { merge: true }
        );
    }
  };

  const channelLike = async (user_id, channel_id) => {
    // if (!channelIsInFavorites(channel_id)) {
    //   try {
    //     await ChannelUtils.setChannelLike(user_id, channel_id);
    //     getAllChannelsFavoriteByUser();
    //     setStatus(true);
    //   } catch (error) {
    //   }
    // } else {
    //   channelUnLike(user_id, channel_id);
    // }
    //   if (!hasChannel) {
    //     getAllChannelsFavoriteByUser(user_id, false);
    //     if (channelFavorites) {
    //       app
    //         .firestore()
    //         .collection("users")
    //         .doc(user_id)
    //         .set(
    //           {
    //             channels: [...channelFavorites, { user_id, channel_id }],
    //           },
    //           { merge: true }
    //         );
    //     }
    //   } else {
    //     channelUnLike(user_id, channel_id)
    //   }
    // }))
    channelIsInFavorites(user_id, channel_id, (hasChannel) => {
      if (!hasChannel) {
        getAllChannelsFavoriteByUser(user_id, false);
        if (channelFavorites) {
          app
            .firestore()
            .collection("users")
            .doc(user_id)
            .set(
              {
                channels: [...channelFavorites, { user_id, channel_id }],
              },
              { merge: true }
            );
        }
        getAllChannelsFavoriteByUser(user_id, true);
      } else {
        channelUnLike(user_id, channel_id);
      }
    });
    return;
  };

  const channelUnLike = async (user_id, channel_id) => {
    getAllChannelsFavoriteByUser(user_id, false);
    if (channelFavorites) {
      app
        .firestore()
        .collection("users")
        .doc(user_id)
        .set(
          {
            channels: channelFavorites.filter((item) => {
              return item.channel_id !== channel_id;
            }),
          },
          { merge: true }
        );
    }
    getAllChannelsFavoriteByUser(user_id, true);
    return;
  };

  const showUnLike = async (user_id, spectacle_id) => {
    try {
      await ShowsService.setShowUnLike(user_id, spectacle_id);
      getShowsByUserFav();
      setStatus(false);
    } catch (error) {}
  };

  return (
    <Shows.Provider
      value={{
        show,
        channels,
        channel,
        showWithLink,
        shows,
        getShow,
        lancamentos,
        status,
        emAltaList,
        adquiridos,
        espetaculosFavoritos,
        showsSignatures,
        watchShow,
        getShowWithLink,
        getShowById,
        getShowsByUser,
        getShowsByUserFav,
        getAllChannels,
        isInFavorites,
        showLike,
        showUnLike,
        channelLike,
        channelUnLike,
        channelIsInFavorites,
        getAllChannelsFavoriteByUser,
        channelFavoritesDeep,
        hasBeenWatched,
        alreadyWatched,
        showsByCategory,
        getShowsByCategory,
        categories,
        subCategories,
        getCategories,
        getSubCategories,
        setShowsByCategory,
        isInFavoritesChannel,
      }}
    >
      {props.children}
    </Shows.Provider>
  );
}

export const optionsToast = {
  position: "top-center",
  autoClose: 5000,
  hideProgressBar: true,
  closeOnClick: true,
  pauseOnHover: true,
  draggable: false,
  progress: undefined,
};

export const useShows = () => useContext(Shows);
